export default {
	data() {
		return {
			system:this.systemInfo(),
			location:{},
			locationName:'请选择',
			select:false,
			videoPath:'',
			goods:{},
			videodesc:''
		}
	},
	onShow() {
		let goodsJson = uni.getStorageSync("goods");
		if(goodsJson){
			this.goods = JSON.parse(goodsJson);
			uni.removeStorage({
				key:'goods'
			});
		}
		
		
	},
	methods: {
		close(){
			uni.switchTab({
				
				url:"/pages/video/main"
			
			});
		},
		chooseLocation(){
			uni.chooseLocation({
				success: (res) =>  {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					this.location = res;
					this.locationName = res.name;
				}
			});
		},
		selectGoods(){
			this.select = true;
		},
		hideModal(){
			this.select = false;
		},
		selectVideo(){
			uni.chooseVideo({
				success:(res) => {
					this.videoPath = res.tempFilePath;
				}
			})
		},
		reselectVideo(){
			uni.showActionSheet({
				itemList: ['重新选择'],
				success: (res) => {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					
						this.selectVideo();
					
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		newgoods(){
			uni.navigateTo({
				url:'/pages/add/addform',
				success:() => {
					this.hideModal();
				}
			})
		},
		goodslist(e){
			let type = e.currentTarget.dataset.type;
			uni.navigateTo({
				url:'/pages/add/goodslist?type='+type,
				success:()=>{
					this.hideModal();
				}
			})
		},
		submit(){
			if(!this.goods.goodsTitle){
				uni.showToast({
					icon:'none',
					title:'请选择商品'
				});
				return;
			}
			
			if(this.videoPath.trim().length<=0){
				uni.showToast({
					icon:'none',
					title:'请上传短视频'
				});
				return;
			}
			if(this.videodesc.trim().length<=0){
				uni.showToast({
					icon:'none',
					title:'请填写推荐理由'
				});
				return;
			}
			if(!this.location.name){
				uni.showToast({
					icon:'none',
					title:'请选择地理位置'
				});
				return;
			}
			uni.showLoading({
				title:"发布中",
				mask:true
			});
			uni.uploadFile({
				url: 'http://www.dpsoft.top/upload.jsp', 
				filePath: this.videoPath,
				name: 'file',
				success: (uploadFileRes) => {
					console.log(uploadFileRes.data);
					let data = JSON.parse(uploadFileRes.data);
					if("ok" == data.status){
						let webpathSuffix = data.data;
						let url = "http://www.dpsoft.top/"+webpathSuffix;
						let data = {};
						data.videopath = url;
						data.goodsid = this.goods.goodsId;
						data.goodsimg = this.goods.goodsImg;
						data.goodsplatform = this.goods.platform;
						data.goodstitle = this.goods.goodsTitle;
						data.goodsprice = this.goods.goodsPrice;
						data.goodsnick = this.goods.goodsSeller;
						data.goodsarea = this.goods.goodsArea;
						data.goodsurl = this.goods.goodsUrl;
						data.videodesc = this.videodesc;
						data.locationname = this.location.name;
						data.locationaddr = this.location.address;
						data.locationlat = this.location.latitude;
						data.locationlng = this.location.longitude;
						data.coverImgUrl = this.goods.goodsImg;
						
						this.postJson({
							path: 'video_publish.jsp',
							data:data,
							success:(res) => {
								if("ok" == res.data.status){
									uni.navigateBack({
										
									});
								}else{
									uni.showToast({
										icon:'none',
										title:data.msg
									});
								}
							},
							complete: ()=> {
								uni.hideLoading();
							}
						});
						
					}else{
						uni.hideLoading();
						uni.showToast({
							icon:'none',
							title:data.msg
						});
					}
					
				},
				fail: (e)=> {
					uni.hideLoading();
				}
			})
		}
	}
}
